




































































































































@import '../../../../../../../packages/craftcms-sass/mixins';

.plugin-details-header {
  @apply .leading-normal .overflow-hidden .mb-1;
  max-height: 4.75em;

  .plugin-name {
    @apply .flex .mb-1;

    .edition-badge {
      @apply .ml-2;
    }
  }
}

.plugin-card {
  box-sizing: border-box;

  &:hover {
    strong {
      color: $linkColor;
    }
  }

  .plugin-icon {
    img {
      width: 60px;
      height: 60px;
    }
  }

  .installed {
    @apply .absolute;
    top: 14px;
    @include right(18px);
    color: #ccc;
  }
}

.ps-grid-plugins {
  .plugin-card {
    @apply .h-full;
  }
}
