























.plugin-editions {
  @apply .py-6;

  .plugin-editions-edition {
    &:not(:last-child) {
      @apply .mb-6;
    }
  }
}

@media (min-width: 992px) {
  .plugin-editions {
    @apply .flex .-mx-4 .justify-center;

    .plugin-editions-edition {
      @apply .w-1/3 .mx-4;

      &:not(:last-child) {
        @apply .mb-0;
      }
    }
  }
}
