



























.screenshots {
  @apply .overflow-auto .flex .-mx-4;
  -webkit-overflow-scrolling: touch;

  .screenshot {
    @apply .px-4 .flex-no-shrink .flex-no-grow;
    flex-basis: 90%;

    img {
      @apply .rounded;
    }
  }
}

@media (min-width: 672px) {
  .screenshots {
    .screenshot {
      flex-basis: 45%;
    }
  }
}

@media only screen and (min-width: 1400px) {
  .screenshots {
    .screenshot {
      flex-basis: 31%;
    }
  }
}
