






















@import '../../../../../../../packages/craftcms-sass/mixins';

.info-hud {
  @apply .inline-block;

  .v-popover {
    @apply .text-grey;
    display: inline-block;
    line-height: 0;

    &:hover,
    &.open {
      @apply .cursor-pointer .text-blue;
    }

    & > span {
      outline: none;
    }
  }
}

.tooltip {
  max-width: 250px;
}
