










































































.plugin-changelog {
  @apply .mb-8;

  &.collapsed {
    @apply .relative .overflow-hidden;
    height: 400px;

    .more {
      @apply .block .absolute .pin-b .w-full .pb-4;
      padding-top: 200px;
      background: rgb(255, 255, 255);
      background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 1) 80%
      );
    }
  }

  .more {
    @apply .text-center;

    a {
      @apply .inline-block;
    }
  }

  .changelog-release:last-child {
    @apply .border-b-0;
  }
}
