



















@import '../../../../../../../packages/craftcms-sass/mixins';

.c-btn {
  &.license-status {
    &:hover {
      cursor: default;
    }

    &.installed {
      color: $successColor;
      border-color: $successColor;
    }

    &.licensed {
      color: $noticeColor;
      border-color: $noticeColor;
    }
  }
}
